$main:#ff8f1f;
$white:#FFF;
$black:#000;
$dark:#111111;
$dark-page:#010e11;
$gray:#acacac;
$gray-link:#5d5d5d;
$nav-back:#03151a;
$white-gray:#f1f1f1;
body {
  color: $white !important;
  background: $dark-page;
}

.navigation-bar {
  background-color: $nav-back;
    .main-navbar {
      .navbar {
        .nav-item {
          &.active {
            .nav-link {
              color: $main;
            }
          }
          .nav-link {
            color: $gray;
            &:hover {
              color:$main;
            }
            .counts-notifications,.counts-message {
              background: $main;
              color: $dark-page;

            }
          }
        }
        .nav-item.dropdown {
          .dropdown-toggle {
            &::after {
              color: $main;
            }
          }
          .dropdown-menu {
            .dropdown-item {
              &:active {
                background: $main;
              }
              &:hover {
                background: $main;
                color: $white;
              }
            }
          }
        }
      }
    }
}

.home-page {
    background: $dark-page;
    color: $white;
}
.post {
        background: $nav-back;

        .post-content {
            .text-content {
                .question {
                    color: $gray;
                    .vertical-bar {
                        background: rgba($main,.5);
                    }
                    .username {
                        color: $main;
                    }
                    .time {
                        &:before {
                            background: $gray;
                        }
                    }
                }
                .reply {
                    color: $gray;

                    .username {
                        color: $gray;
                    }
                }
            }
        }
        .options {
            a {
                color: $gray;
            }
        }
        .actions {
            color: $gray;
                & > div {
                &:hover {
                    color: $main;
                }
            }
        }
    }

.settings {
    background: $nav-back;
    .main-column {
        section {
            header {
                background: $dark-page;
                .title-action {
                    color: $gray;
                }
            }
            .profile-user {
                form {
                    .btn-submit {
                        background: $dark-page;
                        color: $gray;
                    }
                }
            }

                .changed-btn {
                    color: $main;
                    border-bottom: 1px solid $dark-page;
                }
            .delete-acc {
                border: none;
            }
        }
    }
}
.pop-up-setting {
    background: rgba($dark-page,.9);
    .exit {
        color: $white;
    }
    .edit-user{
        background: $nav-back;
        .header-title {
            background: $dark-page;
            color:$gray;
        }
        label {
            color: $gray;
        }
        .form-control {
            border:1px solid #2c464d;
        }
        .btn-submit {
            background: $dark-page;
            color:$gray;
        }
    }
}
